import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { mode } from './../../environments/environment';

import { Article } from '../arts/art';
import { ArtsService } from '../arts/arts.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  artigos: Article[] = [];

  constructor(private artsService: ArtsService, private router: Router) { }

  ngOnInit() {
    mode.admin = true;
    this.artsService.getArts()
    .subscribe(artigos => this.artigos = artigos);
  }

  gotoDetail(art: Article) {
    let link = `/artigo/${art.id}`;
    this.router.navigateByUrl(link, { skipLocationChange: true });
  }

}



