import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { mode } from './../../environments/environment';
import { Article } from '../arts/art';
import { ArtsService } from '../arts/arts.service';

@Component({
  selector: 'app-site',
  templateUrl: './site.component.html',
  styleUrls: ['./site.component.css']
})
export class SiteComponent implements OnInit {

  profileTextOne = '';
  profileTextTwo = '';
  profileTextThree = '';

  articleTextOne = '';
  articleTextTwo = '';

  arts: Article[] = [];

  constructor(private artService: ArtsService) {

    let profOne = '1234567890 0 ';
    for(let x = 1; x < 5 ;x++){
       profOne = profOne.concat(profOne);
    }
    this.profileTextOne = profOne;
    this.profileTextTwo = profOne;
    this.profileTextThree = profOne;

   }

  ngOnInit() {
    mode.admin = false;

    this.artService.getArts()
    .subscribe(artigos => {
      this.arts = artigos;
      this.articleTextOne = this.arts[0].desc;
      this.articleTextTwo = this.arts[1].desc;
    });
  }

}



