import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { User } from '../user';
import { UserService } from '../user.service';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {

  user: User = new User();

  private idUser: Number;

  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private userService: UserService) { }

  ngOnInit() {
    this.getUser();
  }

  isAddMode() {
    return this.idUser==0;
  }

  private getUser() {
    let id = this.activatedRoute.params.subscribe(params => {
      this.idUser = params['id'];
      
      if (this.isAddMode()) {
        this.user.id = 0;
        this.user.name = '';
        this.user.mail = '';
        this.user.pass = '';
        this.user.admin = `false`;
      }
      else{ 
        this.userService.getUser(this.idUser)
               .subscribe(user => this.user = user,
                response => {
                if (response.status == 404) {
                     this.router.navigate(['NotFound']);
                }
            });
    }

   });
  }

  gotoUsers(){
    let link = `/users`;
    this.router.navigateByUrl(link);
  }

  cancel(){
    this.gotoUsers();
  }

  delete() {
    // let msg = `Deseja remover ${this.artigo.name}?`;
    // this._modalService.activate(msg).then(responseOK => {
    //   if (responseOK) {
    //     this.cancel(false);
    //     this._artigoService.deleteArtigo(this.artigo)
    //       .subscribe(() => {
    //         this._toastService.activate(`Removido ${this.artigo.name}`);
    //         this.location.back();
    //       });
    //   }
    // });

      this.userService.deleteUser(this.user.id)
          .subscribe(() => {
            console.log('deleted');
            this.gotoUsers();
      });
  }

  save() {
    
    if (this.user.id == 0) {
      this.userService.addUser(this.user)
        .subscribe(art => {
          console.log('added done');
          this.gotoUsers();
        });
    }
    else {
      this.userService.updateUser(this.user)
      .subscribe(art => {
        console.log('update done');
        this.gotoUsers();
      });
    }

  }

}



