import { Component, Input, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { Article } from '../art';
import { ArtsService } from '../arts.service';

@Component({
  selector: 'app-art',
  templateUrl: './art.component.html',
  styleUrls: ['./art.component.css']
})
export class ArtComponent implements OnInit {

  art: Article = new Article();
  
  private idArticle: Number;

  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private artsService: ArtsService) {

   }

  ngOnInit() {
    this.getArtigo();
  }

  isAddMode() {
    return this.idArticle==0;
  }

  private getArtigo() {
    let id = this.activatedRoute.params.subscribe(params => {
      this.idArticle = params['id'];
      
      if (this.isAddMode()) {
        this.art.id = 0;
        this.art.title = '';
        this.art.title= '';
      }
      else{ 
        this.artsService.getArt(this.idArticle)
               .subscribe(art => this.art = art,
                response => {
                if (response.status == 404) {
                     this.router.navigate(['NotFound']);
                }
            });
    }

   });
  }

  gotoArt(){
    let link = `/artigos`;
    this.router.navigateByUrl(link);
  }

  cancel() {
    this.gotoArt();
  }

  delete() {
    // let msg = `Deseja remover ${this.artigo.name}?`;
    // this._modalService.activate(msg).then(responseOK => {
    //   if (responseOK) {
    //     this.cancel(false);
    //     this._artigoService.deleteArtigo(this.artigo)
    //       .subscribe(() => {
    //         this._toastService.activate(`Removido ${this.artigo.name}`);
    //         this.location.back();
    //       });
    //   }
    // });

      this.artsService.deleteArt(this.art.id)
          .subscribe(() => {
            console.log('deleted');
            this.gotoArt();
      });
  }

  save() {
    
    if (this.art.id == 0) {
      this.artsService.addArt(this.art)
        .subscribe(art => {
          console.log('added done');
          this.gotoArt();
        });
    }
    else {
      this.artsService.updateArt(this.art)
      .subscribe(art => {
        console.log('update done');
        this.gotoArt();
      });
    }

  }

}


