import { Injectable } from '@angular/core';
import { Http } from '@angular/http';

import 'rxjs/add/operator/map';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';

import { Observable } from 'rxjs/Rx';

import { environment, auth } from './../../environments/environment';
import { User } from 'app/users/user';


@Injectable()
export class UserService {

  private url: string = `${environment.apiEndpoint}users`;
  private urlName: string = `${environment.apiEndpoint}userByName`;
  private userModel: User;

  constructor(private http: Http) { }

  public getUsers(){
    return this.http.get(this.url)
      .map(res => res.json());
  }

  public getUser(id){
    return this.http.get(this.getUserUrl(id))
      .map(res => res.json());
  }

  public addUser(user){
    return this.http.post(this.url, user)
      .map(res => res.json());
  }

  public updateUser(user){
    return this.http.put(this.getUserUrl(user.id), user)
      .map(res => res.json());
  }

  public deleteUser(id){
    return this.http.delete(this.getUserUrl(id))
      .map(res => res.json());
  }

  public async signIn(user): Promise<any> {

      const response = await this.http.get(this.getUserNameUrl(user.name)).toPromise();
      this.userModel = response.json();

      if(this.userModel.name==user.name && this.userModel.pass==user.pass) {
        auth.loggedIn = true;
      }

    return response;

  }


  private getUserUrl(id){
    return this.url + "/" + id;
  }

  private getUserNameUrl(name){
    return this.urlName + "/" + name;
  }

}



