import { Injectable } from '@angular/core';
import { Http } from '@angular/http';

import 'rxjs/add/operator/map';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';
import { Observable } from 'rxjs/Rx';

import { environment } from './../../environments/environment';

@Injectable()
export class ArtsService {

  private url: string = `${environment.apiEndpoint}arts`;

  constructor(private http: Http) { }

  public getArts() {
    return this.http.get(this.url)
    .map(res => res.json());
  }

  public getArt(id){
    return this.http.get(this.getArtUrl(id))
      .map(res => res.json());
  }

  public addArt(art){
    return this.http.post(this.url, art)
      .map(res => res.json());
  }

  public updateArt(art){
    return this.http.put(this.getArtUrl(art.id), art)
      .map(res => res.json());
  }

  public deleteArt(id){
    return this.http.delete(this.getArtUrl(id))
      .map(res => res.json());
  }

  private getArtUrl(id){
    return this.url + "/" + id;
  }


}



