import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { mode } from './../../../environments/environment';

import { Article } from '../art';
import { ArtsService } from '../arts.service';

@Component({
  selector: 'app-art-list',
  templateUrl: './art-list.component.html',
  styleUrls: ['./art-list.component.css']
})
export class ArtListComponent implements OnInit {

  arts: Article[] = [];

  constructor(private artService: ArtsService, private router: Router) { }

  ngOnInit() {
    mode.admin = true;
    this.artService.getArts()
    .subscribe(artigos => this.arts = artigos);
  }

  gotoNew() {
    let link = `/artigo/0`;
    this.router.navigateByUrl(link, { skipLocationChange: true });
  }

  gotoDetail(art: Article) {
    let link = `/artigo/${art.id}`;
    this.router.navigateByUrl(link, { skipLocationChange: true });
  }
}



